.desktop {
  display: block;
}

.mobile {
  display: none;
}

.color-blue {
  color: #001e53;
}

.bg-red {
  background-color: #ec6666;
}

html, body {
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: scandia-web, sans-serif;
}

html {
  scroll-behavior: smooth;
}

#app {
  opacity: 0;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

#app.active {
  height: 100%;
  overflow: visible;
}

p {
  font-family: scandia-web, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.lottie-player {
  width: 100%;
  height: 100%;
  transition: opacity 1s;
  position: absolute;
  top: 0%;
}

.lottie-player.active {
  top: -10%;
  opacity: 1 !important;
}

.phase-two {
  opacity: 0;
  transition: all 2s;
}

.loader {
  width: 150px;
  height: 150px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.loader img {
  width: 100%;
  animation-name: pulse;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes pulse {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.1);
  }
}

.section-main {
  width: 100%;
  height: 100vh;
  z-index: 1;
  background-color: #000;
  position: fixed;
}

.main-fhs {
  height: 167px;
  width: 167px;
  position: absolute;
  top: 40%;
  left: 16%;
  transform: translate3d(-50%, -50%, 0);
}

.main-fhs img {
  width: 100%;
}

.main-abs {
  color: #fff;
  font-size: 40px;
  font-weight: 700;
  line-height: 40px;
  position: absolute;
  top: 40%;
  right: 16%;
  transform: translate3d(50%, -50%, 0);
}

.main-abs img {
  width: 100%;
}

.main-details {
  color: #fff;
  width: 100%;
  text-align: center;
  font-size: 32px;
  font-weight: 500;
  position: absolute;
  top: 80%;
  left: 0;
}

.down-arrow {
  width: 46px;
  opacity: 1;
  margin: 20px auto 0;
  transition: all .2s;
  animation-name: arrowBounce;
  animation-duration: .5s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  position: relative;
}

.down-arrow img {
  width: 100%;
}

@keyframes arrowBounce {
  from {
    transform: translate3d(0%, 0, 0);
  }

  to {
    transform: translate3d(0%, 6px, 0);
  }
}

.section-contact {
  text-align: center;
  width: 100%;
  height: auto;
  z-index: 2;
  color: #fff;
  padding: 100px 0 0;
  font-size: 26px;
  transition: all .4s cubic-bezier(.34, 1.56, .64, 1);
  position: relative;
  bottom: 0;
  transform: translateY(100vh);
}

#contact.active {
  transform: translateY(calc(100vh - 40px));
}

.footer-title {
  margin-bottom: 68px;
}

.footer-icon-savills {
  height: 102px;
  width: auto;
  margin: 4px 0 34px;
}

.footer-icon-obi {
  height: 106px;
  width: auto;
  margin: 0 0 34px;
}

.footer-contact-detail {
  font-size: 20px;
  font-weight: 400;
}

.footer-contact-name {
  font-weight: 500;
}

.footer-base {
  box-sizing: border-box;
  width: 100%;
  padding: 0 40px 20px;
  position: relative;
}

.footer-divide {
  height: 1px;
  width: 100%;
  background-color: #fff;
  margin: 100px 0 24px;
}

.footer-credit {
  color: #fff;
  width: 100%;
  text-align: center;
  font-family: scandia-web, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  position: relative;
}

.section-contact a:visited, .section-contact a {
  color: #fff;
  text-decoration: none;
  transition: all .2s;
}

@media (max-width: 1399.98px) {
  .main-details {
    font-size: 30px;
    top: 75%;
  }

  .main-fhs {
    height: 130px;
    width: 130px;
  }

  .main-abs {
    font-size: 35px;
    line-height: 35px;
  }

  .down-arrow {
    margin: 40px auto 0;
  }
}

@media (max-width: 1199.98px) {
  .main-details {
    font-size: 30px;
    top: 75%;
  }

  .main-fhs {
    height: 110px;
    width: 110px;
  }

  .main-abs {
    font-size: 32px;
    line-height: 32px;
  }

  .down-arrow {
    margin: 40px auto 0;
  }

  .footer-contact-detail {
    font-size: 17px;
  }
}

@media (max-width: 991.98px) {
  .desktop {
    display: none;
  }

  .mobile {
    display: block;
  }

  .main-details {
    font-size: 20px;
    top: 70%;
  }

  .lottie-player {
    width: 110%;
    top: 50%;
    transform: translate3d(-15%, -50%, 0);
  }

  .main-fhs {
    height: 60px;
    width: 60px;
    top: 20px;
    left: 20px;
    transform: none;
  }

  .main-abs {
    font-size: 20px;
    line-height: 20px;
    top: 30px;
    right: 20px;
    transform: none;
  }

  .section-contact {
    padding: 60px 0 0;
    font-size: 22px;
  }

  .footer-title {
    margin-top: 30px;
  }

  .footer-icon-savills {
    height: 70px;
    margin: 3px 0 12px;
  }

  .footer-icon-obi {
    height: 73px;
    margin: 0 0 12px;
  }

  .footer-contact {
    margin-bottom: 60px;
  }

  .footer-contact-detail {
    font-size: 18px;
  }

  .footer-divide {
    margin: 28px 0 24px;
  }

  .lottie-player.active {
    top: 40%;
    opacity: 1 !important;
  }

  .loader {
    width: 150px;
    height: 150px;
  }
}

@media (max-width: 767.98px) {
  .main-details {
    top: 65%;
  }

  .lottie-player {
    width: 120%;
    transform: translate3d(-30%, -50%, 0);
  }

  .footer-contact-single {
    margin-top: 20px;
  }
}

@media (max-width: 389.99px) {
  .section-main-headline {
    font-size: 24px;
    line-height: 32px;
  }
}

@media (hover: hover) {
  #contact.active:hover {
    transform: translateY(calc(100vh - 50px));
  }

  .section-contact a:hover {
    color: #000;
  }
}

